@import './styles/variables';

body {
    font-family: $sans-serif;
    font-weight: $normal;
    color: $txt-primary;
    margin: 0;
    overflow: hidden;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    code {
        font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
    }

    .light {
        font-weight: $light;
    }

    .bold {
        font-weight: $bold;
    }

    .countdown-text {
        display: block;
        font-size: 1em;
        line-height: 1.5em;
        letter-spacing: 3px;
    }

    .circle {
        border-radius: 50%;
    }

    .abs {
        position: absolute;
    }

    .back-btn {
        left: 10px;
        top: 10px;
    }

}

h1.title{
    grid-area: title;
    font-size: 1.7em;
    margin-top: 1%;
}

.main-display-wrapper {
    box-sizing: content-box;
    position: relative;
    height: 100%;
    width: inherit;
 }

 .main-display {
    display: flex;
    height: 100%;
    flex-wrap: nowrap;
    position: absolute;
    width: 100vw;
    left: 42vw;
 }

 .next-button {
    font-size: 2em;
    margin: 0;
    border: 1px solid #202020;
    padding: 5px 15px;
    border-radius: 5px;
    background: lightgreen;
    position: absolute;
    right: 15%;
    bottom: 13%;
    border: 1px solid black;
 }

 .timer {
     align-self: center;
 }

 .timer h1 {
     font-size: 2em;
     margin: 0;
     border: 1px solid #202020;
     padding: 5px 15px;
     border-radius: 5px;
 }

 .buttons {
    display: flex;
    flex-direction: column;
 }

.justify-center {
    justify-self: center;
}

h1 {
    font-size: 1em;
}
h2 {
    font-size: 0.8em;
}
h3 {
    font-size: 0.6em;
}

